:root {
    --primary: #fff;
  }
  
  .btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    cursor: pointer;
  }
  
  .btn--primary {
    background-color: #fff;
    background-color: var(--primary);
    color: #242424;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
  }
  
  .btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
  }
  
  .btn--medium {
    padding: 8px 20px;
    font-size: 18px;
  }
  
  .btn--large {
    padding: 12px 26px;
    font-size: 20px;
  }
  
  .btn--large:hover,
  .btn--medium:hover {
    transition: all 0.3s ease-out;
    background: #fff;
    color: #242424;
    transition: 250ms;
  }

  .btn--large,
  .pedido{
    background-image: linear-gradient(360deg, #eda642 50%, #edba42, #ffe68a);
    color: white;
    transition: all 0.3s ease-out;
  }
  .btn--large:hover,
  .pedido:hover{
    transition: all 0.3s ease-out;
    background-image: linear-gradient(360deg, #eda642 80%, #edba42, #ffe68a);
    color: #242424;
  }

  .btn--pedidos{
    border-radius: 5px;
    border: 1px solid transparent;
    margin: 0;
    padding: 0;
    font-size: 28px;
    width: 100%;
    height: 45px;
  }
