}
@keyframes rotation {
    0%{background-position: 0;}
    7.5555%{background-position: 0;}
    26.3055%{background-position: 45%;}
    52.7777%{background-position: 45%;}
    69.4444%{background-position: 90%;}
    75.0000%{background-position: 90%;}
    85.4166%{background-position: 130%;}
    86.1111%{background-position: 130%;}
    100%{background-position: 0;}
}

